@import url(https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,600,700,800,900);
html {
    --background: #212226;
    --primary: tomato;
    --line: #a6a7b5;
    overflow-x: hidden;
}

svg{
    background: tomato;
    background: var(--primary);
    border-radius: 50%;
    fill:#212226;
    fill:var(--background);
    padding: 2vh;
    height: 3vh;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    transition: 0.5s ease;
}

svg.left{
    left:20vw;
}
svg.right{
    right:20vw;
}

.title{
    display: block;
    margin: 0;
}

h1{
    text-align: center;
    padding-top: 5vh;
}

body{
    padding:0;
    margin: 0;
    background: #212226;
    background: var(--background);
    font-family: Montserrat;
    letter-spacing: 0.03em;
    color: white;
}

.shadow{
    position: absolute;
    top:0;
    width: 15vw;
    height: 100vh;
}

.shadow.left{
    left:0;
    box-shadow: 100px -100px 100px 10px #212226 inset;
    box-shadow: 100px -100px 100px 10px var(--background) inset;
}

.shadow.right{
    right:0;
    box-shadow: -100px -100px 100px 10px #212226 inset;
    box-shadow: -100px -100px 100px 10px var(--background) inset;
}

article {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: left;
            justify-content: left;
    padding: 5vh 20vw;
    overflow-x:scroll;
}

article::-webkit-scrollbar { width: 0 !important }

.article { overflow: -moz-scrollbars-none; }

.timeline {
  display: grid;
  grid-auto-columns: -webkit-max-content;
  grid-auto-columns: max-content;
  grid-auto-flow: column;
  grid-template-rows: auto auto;
  z-index:0;
}

.timeline .item {
  grid-column: span 2;
  display: -webkit-flex;
  display: flex;
  padding: 0 1rem;
}

.timeline .container{
    min-width: 20vw;
    transition: 1s ease;
}

.timeline .item:nth-child(2) {
  grid-column: span 3;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.timeline .item .content {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-left: 1px dashed #a6a7b5;
  border-left: 1px dashed var(--line);
  position: relative;
  background: #212226;
  background: var(--background);
  cursor: pointer;
}

.timeline .item .container.highlighted .content:after{
  border-color: tomato;
  border-color: var(--primary);
}

.timeline .item .content:hover:after,.timeline .item .content.active:after{
    background-color: tomato;
    background-color: var(--primary);
    border-color: tomato;
    border-color: var(--primary);
}

.timeline .item .content::after {
  content: "";
  display: block;
  border: 1px solid #a6a7b5;
  border: 1px solid var(--line);
  background: #212226;
  background: var(--background);
  height: 1vw;
  width: 1vw;
  position: absolute;
  left: calc(-.5vw - 1px);
  border-radius: 50%;
  transition: 0.5s ease;
}

.timeline .item .content .title {
  background: #212226;
  background: var(--background);
  color: #a6a7b5;
  color: var(--line);
  padding-bottom: 0.25em;
  position: relative;
  left: -0.25rem;
  text-transform: uppercase;
  font-size: 0.8rem;
  font-weight: 600;
  transition: 1s;
}

.timeline .item .content .date {
  background: #212226;
  background: var(--background);
  margin:0;
  position: relative;
  left: -0.25rem;
  color: #f4f4f6;
  transition: 1s;
}


.timeline .item:nth-child(odd) .content {
  padding-bottom: 5vh;
}
.timeline .item:nth-child(even) .content {
  padding-top: 5vh;
}

.timeline .item:nth-child(odd) .container {
  border-bottom: 0.5vh solid #a6a7b5;
  border-bottom: 0.5vh solid var(--line);
  position: relative;
  z-index: 2;
}
.timeline .item:nth-child(even) .container {
  border-top: 0.5vh solid #a6a7b5;
  border-top: 0.5vh solid var(--line);
  top: -0.5vh;
  position: relative;
  z-index: 2;
}

.timeline .item:nth-child(odd) .content::after {
  bottom: calc(-.5vw  - 2px);
}
.timeline .item:nth-child(even) .content::after {
  top: calc(-.5vw  - 2px);
}

.timeline .item:nth-child(odd) .content .date:last-child {
  padding-bottom: 0.5rem;
}
.timeline .item:nth-child(even) .content .title {
  padding-top: 0.5rem;
}


.timeline .item .container.highlighted{
  border-top-color: tomato !important;
  border-top-color: var(--primary) !important;
  border-bottom-color: tomato !important;
  border-bottom-color: var(--primary) !important;
}
.timeline .item .content.active{
  border-left-color: tomato;
  border-left-color: var(--primary);
}

.timeline .item .content.active .title{
  color: tomato;
  color: var(--primary);
}

.info{
    padding: 5vh 30vw;
    width: 40vw;
    position: relative;
}

.info h1{
    text-align: left;
    color: tomato;
    color: var(--primary);
    padding:0;
    margin-top: 0;
}

.info p{
    font-style: italic;
}

.info .content{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    width: 40vw;
}

.info .content p{
    font-style: normal;
    display: block;
}

.info .content img{
    object-fit: cover;
    width:40%;
    height: 50%;
}

.info svg:hover{
  -webkit-transform: scale(1.1) translateY(-50%);
          transform: scale(1.1) translateY(-50%);
}

.info svg:active{
  -webkit-transform: scale(1) translateY(-50%);
          transform: scale(1) translateY(-50%);
}

.info .container.animate{
    -webkit-animation: fade 1s linear;
            animation: fade 1s linear;
}

.info img{
  padding-left:5%;
}

@-webkit-keyframes fade{
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

@keyframes fade{
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

@media only screen and (max-width: 767px){
    .shadow{
      display: none;
    }

    article{
      padding: 5vh 0;
    }

    h1{
      margin: 0;
      padding-bottom: 3vh;
    }

    .info{
      padding: 0vh 10vw 10vh;
      width: auto;
    }

    .info .content{
      width: auto;
    }

    svg{
      height: 2.5vh;
      padding: 1.5vh;
      bottom: 0;
      top: auto;
    }

    svg:hover{
      -webkit-transform: translateY(-50%) scale(1) !important;
              transform: translateY(-50%) scale(1) !important;
    }

    svg.left{
      left:32vw;
    }
    svg.right{
      right:32vw;
    }

}

